import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_MOVE_COLS = Symbol('TABLE_MOVE_COLS');

export function moveTableCols(
  table: Table,
  originIndexRange: {
    start: number;
    end: number;
  },
  targetIndex: number,
) {
  return {
    type: TABLE_MOVE_COLS,
    payload: {
      node: table,
      originIndexRange,
      targetIndex,
    },
  };
}

interface TableMoveColsPayload {
  node: Table,
  targetIndex: number;
  originIndexRange: {
    start: number;
    end: number;
  },
}

interface TableMoveColsAction {
  type: typeof TABLE_MOVE_COLS;
  payload: TableMoveColsPayload;
}

export function isTableMoveColsAction(
  action: Action,
): action is TableMoveColsAction {
  return action.type === TABLE_MOVE_COLS;
}
